import { Injectable } from "@angular/core";
import { Http } from "@angular/http";
import * as _ from "lodash";
import { Observable } from "rxjs";

@Injectable()
export class StatsService {

  private timestamp: Date = new Date();
  private initDataSet: any = [];
  private dataSource: Observable<any>;
  private max: number = 10;
  private min: number = 0;
  private dataSetCount: number = 10;
  private dataSetLength: number = 50;

  constructor(private http: Http) {
    _.times(this.dataSetCount, (i)=> {
      this.initDataSet.push([]);
      _.times(this.dataSetLength, (j)=> {

        let max = this.max + ((this.dataSetCount - i) * 200);

        this.initDataSet[i].push({
          // "date": new Date(this.timestamp.getTime() + (j * 24 * 60 * 60 * 1000));
          "date": new Date(this.timestamp.getTime() + (j * 1000)),
          "count": Math.floor(Math.random() * (max - this.min)) + this.min
        });
      });
    });

    this.dataSource = Observable.interval(1000).map(()=> {

      _.times(this.dataSetCount, (i)=> {
        let lastElement = this.initDataSet[i][this.initDataSet[i].length - 1];
        let nextTime = lastElement.date.getTime() + 1000;
        let max = this.max + ((this.dataSetCount - i) * 200);

        this.initDataSet[i].push({
          "date": new Date(nextTime),
          "count": Math.floor(Math.random() * (max - this.min)) + this.min
        });

        this.initDataSet[i].splice(0, 1);
      });

      return this.initDataSet;
    });
  }

  getRandomData(): Observable<any> {
    return this.dataSource;
  }
}