import { Injectable } from "@angular/core";
import { DropInPresetBuilder, DialogPreset, DialogPresetBuilder, Modal } from "../lib/vex";
import { DialogRef } from "angular2-modal";
import { config } from "../app.config";

@Injectable()
export class ModalService {

  constructor(private modal: Modal) {
  }

  alert(): DropInPresetBuilder {
    return this.modal.alert()
      .className(config.modalTheme);
  };

  prompt(): DropInPresetBuilder {
    return this.modal.prompt()
      .className(config.modalTheme);

  };

  confirm(): DropInPresetBuilder {
    return this.modal.confirm()
      .className(config.modalTheme);
  };


  open(content, config?): Promise<DialogRef<any>> {
    return this.modal.open(content, config);
  };

  getDialogPresetBuilder(): DialogPresetBuilder<DialogPreset> {
    return new DialogPresetBuilder<DialogPreset>(this.modal)
      .className(config.modalTheme);
  };


}




