import {
  Component, OnInit, Renderer, SimpleChange, OnChanges, Input, ViewChild, ElementRef, AfterViewInit
} from "@angular/core";
import { Http } from "@angular/http";
import { Observable } from "rxjs";


@Component({
  selector: 'previewer',
  template: require('./previewer.component.html'),
  styles: [require('./previewer.component.css')]
})
export class Previewer implements OnInit, OnChanges, AfterViewInit {

  @ViewChild('previewerContainer') previewerContainer: ElementRef;
  @ViewChild('device') device: ElementRef;

  @Input() public previewDevice;
  @Input() public previewScale: number;
  @Input() public previewApp: string;

  public width: string;
  public height: string;
  public hasPreview: boolean = false;

  deviceBoundingBox = {
    top: 114,
    right: 36,
    left: 34,
    bottom: 104,
    imageRatio: 485 / 960,
    width: 485,
    height: 960
  };

  topLeft = {
    top: 0,
    left: 0
  };

  constructor(private renderer: Renderer,
              private elementRef: ElementRef,
              private http: Http) {
  }

  ngOnInit() {
    if (!this.previewScale)
      this.previewScale = 0.75;
  }

  ngAfterViewInit() {
    this.applyHostStyle();

    if (this.previewApp && this.device && this.device['nativeElement'])
      this.reload(this.previewApp);
  }

  ngOnChanges(changes: { [key: string]: SimpleChange }) {
    if (changes['previewApp']) {
      let newSrc = changes['previewApp'].currentValue;
      if (newSrc && this.device && this.device['nativeElement'])
        this.reload(newSrc);
    }

    if (changes['previewScale']) {
      let previewScale = changes['previewScale'].currentValue;
      this.previewScale = previewScale;
      this.applyHostStyle();
    }

    if (changes['previewDevice']) {
      let previewDevice = changes['previewDevice'].currentValue;
      this.previewDevice = previewDevice;
      this.applyHostStyle();
    }
  }

  calculateTopLeft() {
    let imageHeight = this.previewerContainer.nativeElement.clientHeight;
    let heightScale = imageHeight / this.deviceBoundingBox.height;
    // let imageWidth = imageHeight * this.deviceBoundingBox.imageRatio;
    // let marginLeft = (this.previewerContainer.nativeElement.clientWidth - imageWidth) / 2;
    this.topLeft.top = this.deviceBoundingBox.top * heightScale - 2;
    this.topLeft.left = this.deviceBoundingBox.left * heightScale - 2;
  }

  applyHostStyle() {

    if (this.previewerContainer) {
      let imageWidth = this.previewerContainer.nativeElement.clientHeight * this.deviceBoundingBox.imageRatio;
      this.renderer.setElementStyle(this.previewerContainer['nativeElement'], 'width', imageWidth + 'px');
    }

    if (this.device && this.previewDevice) {
      this.calculateTopLeft();
      this.renderer.setElementStyle(this.device['nativeElement'], 'top', (-(this.previewDevice.styles.height * ((1 - this.previewScale) / 2) ) + this.topLeft.top) + 'px');
      this.renderer.setElementStyle(this.device['nativeElement'], 'left', (-(this.previewDevice.styles.width * ((1 - this.previewScale) / 2) ) + this.topLeft.left ) + 'px');
    }
  }

  reload(source) {
    if (!this.device)
      return;

    if (source) {
      this.http
        .get(source + "/index.html")
        .subscribe(
          data => {
            this.hasPreview = true;
            this.renderer.setElementAttribute(this.device['nativeElement'], "src", source);
            setTimeout(this.device['nativeElement'].contentWindow.location.reload, 100);
          },
          err => {
            this.hasPreview = false;
          }
        );
    }
  }
}
