import { Injectable } from "@angular/core";
import { Http, URLSearchParams } from "@angular/http";
import "rxjs/add/operator/toPromise";
import { config } from "../app.config";
import { Observable } from "rxjs";
import * as _ from 'lodash';

@Injectable()
export class AuthService {
  private serverUrl = config.serverUrl;
  private defaultOption = {withCredentials: true};

  constructor(private http: Http) {
  }


  checkEmail(email: string): Observable<any> {
    let url = this.serverUrl + '/checkEmail';

    let params: URLSearchParams = new URLSearchParams();
    params.set("email", email);

    return this.http
      .get(url, _.assign({search: params}, this.defaultOption))
      .map((response) => response.json());
  }

  checkUsername(username: string): Observable<any> {
    let url = this.serverUrl + '/checkUsername';

    let params: URLSearchParams = new URLSearchParams();
    params.set("username", username);

    return this.http
      .get(url, _.assign({search: params}, this.defaultOption))
      .map((response) => response.json());
  }

  checkNickname(nickname: string): Observable<any> {
    let url = this.serverUrl + '/checkNickname';

    let params: URLSearchParams = new URLSearchParams();
    params.set("nickname", nickname);

    return this.http
      .get(url, _.assign({search: params}, this.defaultOption))
      .map((response) => response.json());
  }

  login(identifier: string, password: string): Observable<any> {
    let url = this.serverUrl + '/login';

    return this.http
      .post(url, {identifier: identifier, password: password}, this.defaultOption)
      .map((response) => response.json());
  }

  logout(queryParams: any): Observable<any> {
    let url = this.serverUrl + '/logout';

    return this.http
      .get(url, this.defaultOption)
      .map((response) => response.json());
  }

  oauth(provider: string): void {
    let url = this.serverUrl + '/auth/' + provider;
    window.location.href = url;
  }

  getMyUserInfo(queryParams: any) {
    let url = this.serverUrl + '/me';

    let params: URLSearchParams = new URLSearchParams();
    _.forEach(queryParams, (value, key)=> {
      params.set(key, JSON.stringify(value));
    });

    return this.http
      .get(url, {search: params, withCredentials: true})
      .map((response) => response.json());
  }

}