import { Injectable } from "@angular/core";
import { Http } from "@angular/http";


@Injectable()
export class MockDataService {

  constructor(private http: Http) {
  }

  // query: {offset, limit, count}
  getMockData(query): Promise<any> {
    return new Promise(resolve=> {
      const req = new XMLHttpRequest();
      req.open('GET', `data/100k.json`);
      // req.open('GET', `lib/angular2-data-table/assets/data/100k.json`);

      req.onload = () => {
        var list = JSON.parse(req.response);
        var res = {
          count: list.length,
          list: list.splice(query.query.skip, query.query.limit)
        };
        resolve(res);
      };

      req.send();
    })
  }
}