import { Component, OnInit } from '@angular/core';
import 'hammerjs';

@Component({
  selector: 'material-example',
  template: require('./material-example.component.html'),
  styles: [require('./material-example.component.css')]
})
export class MaterialExample implements OnInit {
  isDisabled: boolean;
  todos: Todos[];
  user: User;
  data: Data[];
  groupValue: string;
  messages: Message[];
  links: any[];
  tiles: any[];
  myValue: any = '100%';
  i: number;
  slideToggleModel: boolean;
  pieOptions: any[];

  constructor() {
    this.i = 0;
  }

  ngOnInit() {
    this.isDisabled = true;
    this.todos = [{
      completed: false,
      name: 'yunseok'
    }, {
      completed: false,
      name: 'hummin'
    }, {
      completed: false,
      name: 'dongho'
    }]
    this.user = {
      agreesToTOS: false
    }
    this.data = [{
      label: 'yunseok',
      value: 'hi'
    }, {
      label: 'hummin',
      value: 'hello'
    }, {
      label: 'dongho',
      value: 'how are you?'
    }]
    this.groupValue = 'hi';
    // list
    this.messages = [{
      from: 'home',
      subject: 'charge',
      message: 'hi. how are you? where are you from'
    }, {
      from: 'home',
      subject: 'charge',
      message: 'hi. how are you? where are you from'
    }, {
      from: 'home',
      subject: 'charge',
      message: 'hi. how are you? where are you from'
    }];
    this.links = [
      'temp', 'temp2', 'temp3'
    ];
    this.tiles = [
      {text: 'One', cols: 3, rows: 1, color: 'lightblue'},
      {text: 'Two', cols: 1, rows: 2, color: 'lightgreen'},
      {text: 'Three', cols: 1, rows: 1, color: 'lightpink'},
      {text: 'Four', cols: 2, rows: 1, color: '#DDBDF1'},
    ];
    setInterval(()=> {
      this.i = this.i + 10;
      this.i = this.i % 100;
    }, 100);
    this.pieOptions = [
      '사과파이', '호두파이', '치즈파이'
    ]
  }

}
export class Todos {
  completed: boolean;
  name: string;
}
export class User {
  agreesToTOS: boolean;
}
export class Data {
  label: string;
  value: string;
}
export class Message {
  from: string;
  subject: string;
  message: string;
}
