import { Component, ViewChild, OnInit } from '@angular/core';
import { FormGroup, FormControl, FormBuilder, Validators } from "@angular/forms";
import { AuthService } from "../../services/auth.service";
import { ModalService } from "../../services/modal.service";
import { EmailValidator } from "../../shared/validators/email-format.validator";
import * as _ from 'lodash';

@Component({
  selector: 'login',
  template: require('./login.component.html'),
  styles: [require('./login.component.css')]
})
export class Login implements OnInit {

  @ViewChild('email') firstInput;

  originalLogin: any;

  loginForm: FormGroup;
  email: FormControl = new FormControl("");
  password: FormControl = new FormControl("");

  result;


  constructor(private fb: FormBuilder,
              private authService: AuthService,
              private modal: ModalService) {

  }

  ngOnInit(): void {
    this.originalLogin = {};
    this.initForm();
    this.configControls();
    this.loadUser();
  }


  initForm() {
    this.loginForm = this.fb.group({
      email: this.email,
      password: this.password,
    });

    this.loginForm.statusChanges
      .subscribe(data => this.onStatusChanges(data));
    this.onStatusChanges(); // (re)set validation messages now
  }

  configControls(): void {

    this.email.setValue(this.originalLogin.email);
    this.email.setValidators(Validators.compose([Validators.required, EmailValidator.isValidFormat]));

    this.password.setValue("");
    this.password.setValidators(Validators.compose([
      Validators.required,
      Validators.minLength(9),
    ]));

  }

  loadUser() {
    this.authService.getMyUserInfo({})
      .subscribe(
        data=>this.result = data,
        error => {
          console.error(error);
          let message = '서버 에러';
          // this.modal.alert()
          //   .message(message)
          //   .showCloseButton(true)
          //   .open();
          this.result = error;
        });
  }

  ngAfterViewInit() {
    this.firstInput.focus();
  }

  onStatusChanges(data?: any) {
    if (!this.loginForm) {
      return;
    }
    const form = this.loginForm;

    for (const field in this.formErrors) {
      // clear previous error message (if any)
      this.formErrors[field] = '';
      const control = form.get(field);

      if (control && control.dirty && !control.valid) {
        const messages = this.validationMessages[field];
        for (const key in control.errors) {
          this.formErrors[field] += messages[key] + ' ';
          break;
        }
      }
    }
  }

  login() {
    let login = _.clone(this.loginForm.value);

    this.authService.login(login.email, login.password)
      .subscribe(
        data=>this.result = data,
        error => {
          console.error(error);
          let message = '서버 에러';
          // this.modal.alert()
          //   .message(message)
          //   .showCloseButton(true)
          //   .open();
          this.result = error;
        });

    return false;
  }

  facebookLogin() {
    this.authService.oauth('facebook');
  }

  googleLogin() {
    this.authService.oauth('google');
    // .subscribe(
    //   data=>this.result = data,
    //   error => {
    //     console.error(error);
    //     let message = '서버 에러';
    //     // this.modal.alert()
    //     //   .message(message)
    //     //   .showCloseButton(true)
    //     //   .open();
    //     this.result = error;
    //   });
  }

  formErrors = {
    'email': '',
    'password': '',
  };

  validationMessages = {
    'email': {
      'required': '이메일은 필수 입력 정보 입니다.',
      'invalidEmail': '맞지않는 이메일 형식입니다.',
    },
    'password': {
      'required': '비밀번호는 필수 입력 정보 입니다.',
    },
  };


}
