import { Injectable } from "@angular/core";
import { Http, URLSearchParams } from "@angular/http";
import "rxjs/add/operator/toPromise";
import { FileUploader } from "../lib/ng2-file-upload/components/file-upload/file-uploader.class";
import { config } from "../app.config";
import * as _ from "lodash";
import { Observable } from "rxjs";


@Injectable()
export class TemplateService {
  private serverUrl = config.serverUrl + '/template';

  constructor(private http: Http) {
  }


  hasPreview(id: number, status: string): Observable<any> {
    let url = this.serverUrl + '/hasPreview';

    let params: URLSearchParams = new URLSearchParams();
    params.set("id", id.toString());
    params.set("status", status);

    return this.http
      .get(url, {search: params})
      .map((response) => response.json());
  }

  findFile(id: number, status: string, fileName: string): Observable<any> {
    let url = this.serverUrl + '/findFile';

    let params: URLSearchParams = new URLSearchParams();
    params.set("id", id.toString());
    params.set("status", status);
    params.set("fileName", fileName);

    return this.http
      .get(url, {search: params})
      .map((response) => response.json());
  }

  findFiles(id: number, status: string): Observable<any> {
    let url = this.serverUrl + '/findFiles';

    let params: URLSearchParams = new URLSearchParams();
    params.set("id", id.toString());
    params.set("status", status);

    return this.http
      .get(url, {search: params})
      .map((response) => response.json());
  }

  clearFiles(id: number): Observable<any> {
    let url = this.serverUrl + '/clearFiles';

    let params: URLSearchParams = new URLSearchParams();
    params.set("_id", id.toString());

    return this.http
      .delete(url, {search: params})
      .map((response) => response.json());
  }

  count(queryParams: any): Observable<any> {
    let url = this.serverUrl + '/count';

    let params: URLSearchParams = new URLSearchParams();
    _.forEach(queryParams, (value, key)=> {
      params.set(key, JSON.stringify(value));
    });

    return this.http
      .get(url, {search: params})
      .map((response) => response.json());
  }


  find(queryParams: any): Observable<any> {
    let url = this.serverUrl + '/find';

    let params: URLSearchParams = new URLSearchParams();
    _.forEach(queryParams, (value, key)=> {
      params.set(key, JSON.stringify(value));
    });

    return this.http
      .get(url, {search: params})
      .map((response) => response.json());
  }

  findOne(queryParams: any): Observable<any> {
    let url = this.serverUrl + '/findOne';

    let params: URLSearchParams = new URLSearchParams();
    _.forEach(queryParams, (value, key)=> {
      params.set(key, JSON.stringify(value));
    });

    return this.http
      .get(url, {search: params})
      .map((response) => response.json());
  }


  create(template: any): Observable<any> {
    return this.http
      .post(this.serverUrl, template)
      .map((response) => response.json());
  }

  update(template: any): Observable<any> {
    return this.http
      .put(this.serverUrl, template)
      .map((response) => response.json());
  }

  remove(id): Observable<any> {
    let params: URLSearchParams = new URLSearchParams();
    params.set("_id", id.toString());

    return this.http
      .delete(this.serverUrl, {search: params})
      .map((response) => response.json());
  }

  getUploader(): FileUploader {
    return new FileUploader({
      url: this.serverUrl + '/upload',
      filters: [
        {
          name: '.DS_Store',
          fn: (file) => {
            let filename = file.name.toLowerCase();
            return filename.indexOf('.ds_store') < 0;
          }
        }
      ]
    });
  }

}