import { Injectable } from "@angular/core";
import { Http, URLSearchParams } from "@angular/http";
import "rxjs/add/operator/toPromise";
import { config } from "../app.config";
import * as _ from "lodash";
import { Observable } from "rxjs";

@Injectable()
export class UserService {
  private serverUrl = config.serverUrl + '/user';

  constructor(private http: Http) {
  }


  count(queryParams: any): Observable<any> {
    let url = this.serverUrl + '/count';

    let params: URLSearchParams = new URLSearchParams();
    _.forEach(queryParams, (value, key)=> {
      params.set(key, JSON.stringify(value));
    });

    return this.http
      .get(url, {search: params})
      .map((response) => response.json());
  }

  find(queryParams: any): Observable<any> {
    let url = this.serverUrl + '/find';

    let params: URLSearchParams = new URLSearchParams();
    _.forEach(queryParams, (value, key)=> {
      params.set(key, JSON.stringify(value));
    });

    return this.http
      .get(url, {search: params})
      .map((response) => response.json());
  }

  findOne(queryParams: any): Observable<any> {
    let url = this.serverUrl + '/findOne';

    let params: URLSearchParams = new URLSearchParams();
    _.forEach(queryParams, (value, key)=> {
      params.set(key, JSON.stringify(value));
    });

    return this.http
      .get(url, {search: params})
      .map((response) => response.json());
  }


  create(user: any): Observable<any> {
    return this.http
      .post(this.serverUrl, user)
      .map((response) => response.json());
  }

  update(user: any): Observable<any> {
    return this.http
      .put(this.serverUrl, user)
      .map((response) => response.json());
  }

  remove(ids: string[]): Observable<void> {
    let params: URLSearchParams = new URLSearchParams();
    params.set("ids", ids.toString());

    return this.http
      .delete(this.serverUrl, {search: params})
      .map((response) => response.json());
  }




}